:root{
  --color-1:#282c34;
  --color-2:#61dafb;
  --color-3:#f0f6f8;
  --color-4:rgb(19, 42, 71);
}

.App {
  text-align: center;
  background-color: var(--color-1);
}

*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.input{
  padding: 5px;
  outline: none;
  width: 90%;
  margin: 10px 0;
}

.button{
  border: 0;
  border-radius: 3px;
  font-weight: bold;
  padding: 8px;
  margin-right: 5px;
  cursor: pointer;
}

.btn-blue{
  background-color:var(--color-2);
  color:var(--color-3);
}

.btn-normal{
  background-color:var(--color-3);
  color: var(--color-1);
}