.item{
    display: inline-block;
    padding: 10px;
    margin: 15px;
    width: 200px;
    vertical-align: top;
    border: 1px solid #222;
    text-align: center;
}

.item .image img{
    height: 330px;
}

.item .title{
    font-weight: bold;
    font-size: 18px;
    height: 60px;
}

.rating{
}

.rating div{
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    gap: 3px;
    padding: .5em 0 1em;
}

.rating div img{
    width: 32px;
}

.rating .select-container{
    display: flex;
    justify-content: space-between;
    padding: 0;
}

.select{
    cursor: pointer;
}

.item .actions{
    width: 100%;
    border-top: solid 1px #bbb;
    padding: 5px 0;
    margin-top: 10px;
}

.item .actions button{
    border: 0;
    border-radius: 3px;
    background-color:rgb(232, 78, 78);
    color: white;
    font-weight: bold;
    padding: 5px;
    width: 100%;
    cursor: pointer;
}