.container {
  background-color: var(--color-4);
  border-bottom: solid 1px #888;
  padding: 10px;
  margin: 0 auto;
}

.container .subcontainer {
  max-width: 820px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  gap: 10px;
}

/* LOGO ------------------------------------------*/

.container-logo {
  color: #fff;
  font-weight: bolder;
  font-size: 22px;
  display: flex;
  align-items: center;
}

.App-logo {
    pointer-events: none;
    width: 60px
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to    {
        transform: rotate(360deg);
    }
}

/* Search ------------------------------------------*/

.search {
  width: 100%;
}

.search input {
    width: 100%;
  padding: 10px;
  font-size: 16px;
}

.actions {
  width: 150px;
  height: 40px;
}

.actions button{
    width: 150px;
    height: 100%;
  }

.actions ul,
.actions ul li {
  margin: 0;
  list-style: none;
}
.actions ul li a {
  display: inline;
  padding: 10px;
}

.new-item-panel-container {
  background-color: #00000090;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  position: fixed;
}

.new-item-panel {
  width: 300px;
  margin: 20px auto;
  background-color: var(--color-3);
  padding: 20px;
  color: var(--color-1);
}

.new-item-panel div {
  padding: 10px 0 20px;
  display: flex;
  justify-content: space-evenly;
}


@media screen and (max-width:720px) {
    .container .subcontainer{
        flex-wrap: wrap;   
        justify-content: space-between; 
    }

    .search{
        order: 2;
    }
}
